//圖片設定-第一區塊
.banner {
    background-image: url(../images/index-background1.png);
    background-repeat: no-repeat;
    background-size: cover;
    color: #650300;
}

.banner-content {
    height: 840px;
}

.specialty img {
    width: 100px;
    height: 100px;
}

//圖片設定-第三區塊
.detail-person1 {
    width: 635px;
    height: 320px;
}

@media(max-width:992px) {
    .detail-person1 {
        width: 58%;
    }
}

@media(max-width:767px) {
    .detail-person1 {
        width: 100%;
    }
}

.detail-person2 {
    width: 635px;
    height: 320px;
}

@media(max-width:992px) {
