.classic-content-ul img {
    width: 44px;
    height: 44px;
}

@media(max-width:992px) {
    .classic-content-ul img {
        width: 36.67px;
        height: 36.67px;
    }
}

.classic-content-u2 img {
    width: 44px;
    height: 44px;
}

@media(max-width:992px) {
    .classic-content-u2 img {
        width: 36.67px;
        height: 36.67px;
    }

}

.classic-content-u3 img {
    width: 48px;
    height: 48px;
}

