    }
}

//標題
h2 {
    font-size: 36px;
    line-height: 1.5;
}

@media(max-width:992px) {
    h2 {
        font-size: 20px;
    }
}

@media(max-width:767px) {
    h2 {
        font-size: 16px;
        line-height: 1.875;
    }
}

@media(max-width:375px) {
    h2 {
        font-size: 8px;
        line-height: 1.5;
    }
}

h3 {
    font-size: 64px;
    line-height: 1.5;
    font-weight: bold;
}

@media(max-width:992px) {
    h3 {
        font-size: 48px;
    }
}

@media(max-width:767px) {
    h3 {
        font-size: 32px;
        line-height: 2.25;
    }
}

@media(max-width:375px) {
    h3 {
        font-size: 24px;
        line-height: 1.5;
    }
}

h4 {
    font-size: 48px;
    line-height: 1.5;
    font-weight: bold;
    color: #650300;
    padding-top: 60px;
    padding-bottom: 40px;
}

@media(max-width:992px) {
    h4 {
        font-size: 32px;
        padding-top: 40px;
        padding-bottom: 32px;
    }
}

@media(max-width:767px) {
    h4 {
        font-size: 24px;
        padding-top: 32px;
        padding-bottom: 20px;
    }

}

@media(max-width:375px) {
    h4 {
        font-size: 20px;
    }
}

@media(max-width:767px) {
    .classic-content {
        font-size: 20px;
        line-height: 2.4;
        padding-top: 32px;
        padding-bottom: 20px;
    }
}

h5 {
    font-size: 24px;
    line-height: 1.5;
    color: #650300;
    padding-top: 16px;
    font-weight: bold;
}

@media(max-width:375px) {
    h5 {
        font-size: 16px;
    }
}

h6 {
    color: #aa0601;
    font-style: italic;
    font-weight: 900;
    font-size: 48px;
    line-height: 1.5;
}

@media(max-width:1060px) {
    h6 {
        font-size: 36px;
    }
}

@media(max-width:992px) {
    h6 {
        font-size: 28px;
        line-height: 2.57;
    }
}

//內文
.specialty p {
    font-size: 16px;
    line-height: 1.5;
    padding-top: 8px;
}

//容器
.specialty {
    background-color: #FBF2F2;
}

.specialty-content {
    column-gap: 30px;
    padding-bottom: 80px;
}

@media(max-width:992px) {
    .specialty-content {
        column-gap: 0px;
        display: flex;
        justify-content: space-evenly;
        padding-bottom: 56px;
    }
}

@media(max-width:767px) {
    .specialty-content {
        padding-bottom: 8px;
    }
}

.specialty-card {
    width: 22%;
}

@media(max-width:992px) {
    .specialty-card {
        width: 41%;
    }
}

@media(max-width:767px) {
    .specialty-card {
        width: 64%;
        padding-bottom: 32px;
    }
}

@media(max-width:375px) {
    .specialty-card {
        width: 85%;
    }
}

//classic
.classic-detail-1 {
    padding-bottom: 32px;
    position: relative;
}

@media(max-width:992px) {
    .classic-detail-1 {
        padding: 0px 31px 8px 24px;
    }
}

.classic-detail-2 {
    padding-bottom: 32px;
    position: relative;
}

@media(max-width:992px) {
    .classic-detail-2 {
        padding: 0px 31px 8px 24px;
        flex-direction: row;
    }
}

.classic-detail-3 {
    padding-bottom: 80px;
    position: relative;
}

@media(max-width:992px) {
    .classic-detail-3 {
        padding: 0px 30px 56px 24px;
    }
}

.classic-content-ul {
    padding-left: 30px;
}

@media(max-width:767px) {
    .classic-content-ul {
        padding-left: 0;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }
}

.classic-content-ul li {
    padding-left: 8px;
}

@media(max-width:992px) {
    .classic-content-ul li {
        padding: 9.67px;
    }
}

.classic-content-u2 {
    padding-right: 30px;
}

@media(max-width:992px) {
    .classic-content-u2 {
        padding-left: 31px;
        padding-right: 0px;
    }
}

@media(max-width:767px) {
    .classic-content-u2 {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }
}

.classic-content-u2 li {
    padding-left: 10px;
}

.classic-content-u3 {
    padding-left: 30px;
}

@media(max-width:767px) {
    .classic-content-u3 {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }
}

.classic-content-u3 li {
    padding-left: 8px;
}

